body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #553055;
}

.App{
  background: #4c2a4c;
  margin: 20px auto;
  width: 90%;
  max-width: 700px;
  color:#eee;
}
.navbar{
  padding: 5px 20px;
  text-align: center;
  background: #6d3d6d;
}
.navbar img{
  height: 120px;
  width: 120px;
}
.navbar h1{
  margin: 0;
  padding: 0;
}
.navbar p{
  margin: 0;
  padding: 13px;
}

.book-list{
  margin: 20px;
}
.book-list ul{
  list-style-type: none;
  margin-left: -40px;
}

.book-list li{
  background: #6d3d6d;
  border-radius: 4px;
  display: block;
  padding: 10px;
  cursor: pointer;
  margin: 20px auto;
  width: 85%;
}

.book-list li:hover{
  opacity: 0.7;
  text-decoration: line-through;
}

.book-list .title{
  font-weight: bold;
  color: #fff;
  font-size: 1.2rem;
}

.book-list .seller{
  font-size: 0.9 rem;
  color:#ddd;
}
.book-list .price{
  color: yellow;
}
 .empty{
   margin: 20px;
   text-align: center;
 }
 .empty h1{
   color: red;
 }
.empty p{
   color: rgb(245, 224, 157);
 }

 form{
   padding: 18px;
 }

 input[type='text']{
   width: 100%;
   padding: 10px;
   box-sizing: border-box;
   margin: 10px 0;
   background: #3c1f3c;
   color: #fff;
   border: 0;
   border-radius: 5px;
   outline-color: blue;
 }

 input[type="submit"]{
  margin: 10px auto;
  border: 0;
  border-radius: 5px;
  padding: 6px 20px;
  display: block;
  color: rgb(249, 249, 250);
  background: rgb(2, 96, 150);
  cursor: pointer;
 }
 input[type='submit']:hover{
  background: rgb(2, 102, 2);
  
 }

 